<template>
<div class="single-subject">
    <!-- <div class="single-img">{{ content.img }}</div> -->
    <img class="single-img" :src="require(`@/assets/${content.img}.png`)" alt="">
    <div class="single-box">
        <p class="single-text">{{ content.title }}</p>
        <div class="single-star-box">
            <div class="single-text">: </div>
            <img class="single-star" :key="index" v-for="index in content.star" src="@/assets/star.png" alt="star">
        </div>
        <div @click.stop="switchFloat" class="single-btn">
            view more
            <transition name="fade">
            <div v-if="isShowFloat" class="single-float">{{ content.text }}</div>
            </transition>
        </div>
    </div>
</div>
</template>

<script>

export default {
  name: 'SingleSubject',
  props: {
    content: {
      type: Object,
      default () {}
    }
  },
  data () {
    return {
      isShowFloat: false
    }
  },
  methods: {
    switchFloat () {
      this.$emit('closeFloat')
      this.isShowFloat = !this.isShowFloat
    },
    closeFloating () {
      this.isShowFloat = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.single-subject {
    width: 360px;
    display: flex;
    margin-bottom: 30px;

    .single {
        padding: 30px;
        display: flex;

        &-img {
            width: auto;
            height: 78px;
            margin-right: 30px;
        }

        &-box {
            width: 100%;
            box-shadow: 1px 1px 9px 0 #f1f7f9;
            background-color: #ffffff;
            margin: 0px 16px;
        }

        &-text {
            font-size: 24px;
            color: $theme-color-dark-gray;
        }

        &-star-box {
            display: flex;
            align-items: center;
        }

        &-star {
            width: 24px;
            color: 24px;
            margin: 0px 0px 0px 4px;
        }

        &-btn {
            font-size: 18px;
            color: $theme-color-dark-blue;
            position: relative;
            text-align: right;
            cursor: pointer;
        }

        &-float {
            width: 100%;
            height: auto;
            z-index: 1;
            position: absolute;
            left: 0px;
            border: 1px solid $theme-color-dark-black;
            padding: 6px;
            background-color: $theme-color-gray;
            color: $theme-color-dark-black;
            text-align: left;
        }

    }

}

@media( max-width: 1210px ) {

    .single-subject {
        width: 300px;

        .single {

        }
    }
}

@media( max-width: 1023px ) {

    .single-subject {

        .single {

            &-img {
                width: auto;
                height: 48px;
            }

            &-text {
                font-size: 18px;
            }

            &-star-box {

            }

            &-star {

            }

            &-btn {
                font-size: 16px;
            }

            &-float {

            }

        }

    }

}

</style>



