<template>
<div id="commonService" class="service-request">
    <div class="service-main">
        <h1 class="title service-title"></h1>
    </div>
    <div class="request">
        <p class="request-text"></p>
        <select
        v-if="type==='enterprise'"
        v-model="form.class"
        class="request-select"
        >
            <option selected value="A">A</option>
            <option value="B-AI ">B-AI </option>
            <option value="C-">C-</option>
            <option value="D-AI ">D-AI </option>
            <option value="E-">E-</option>
            <option value="F">F</option>
            <option value="G">G</option>
        </select>
        <select
        v-if="type==='student'"
        v-model="form.class"
        class="request-select"
        >
            <option selected value="AI">AI</option>
            <option value="AI">AI</option>
            <option value="AI">AI</option>
        </select>
        <div class="request-out-box">
            <div class="request-box">
                <p v-if="type==='enterprise'" class="request-text"></p>
                <p v-if="type==='student'" class="request-text"></p>
                <input v-model="form.company" class="request-input" type="text">
            </div>
            <div class="request-box">
                <p v-if="type==='enterprise'" class="request-text"></p>
                <p v-if="type==='student'" class="request-text"></p>
                <input v-model="form.name" class="request-input" type="text">
            </div>
        </div>
        <div class="request-out-box">
            <div class="request-box">
                <p class="request-text"></p>
                <input v-model="form.number" class="request-input" type="text">
            </div>
            <div class="request-box">
                <p class="request-text"></p>
                <input v-model="form.mail" class="request-input" type="text">
            </div>
        </div>
        <div class="request-out-box">
            <div class="request-box">
                <p class="request-text">/</p>
                <input v-model="form.others" class="request-input" type="text">
            </div>
            <div class="request-box request-btn-box">
                <button @click="sendMail" class="request-btn"></button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'ServiceRequest',
  data () {
    return {
      form: {
        class: '',
        company: '',
        name: '',
        number: '',
        mail: '',
        others: ''
      }
    }
  },
  props: {
    type: { type: String, default: 'enterprise' }
  },
  methods: {
    sendMail () {
      if (this.form.class && this.form.company && this.form.name && this.form.number && this.form.mail) {
        axios.post('/sendMail',
          this.form
        )
          .then(response => {
            alert('')
            console.log(response)
          })
          .catch(error => {
            console.log(error)
          })
      } else {
        alert('')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.service-request {
    display: flex;

    .service-main {
        width: 45%;
        padding: 50px 20px 50px 120px;

        .service-title {
            text-align: left;
        }

        .service-subtitle {
            text-align: left;
        }
    }

    .request {
        width: 55%;
        padding: 50px;

        &-text {
            font-size: 18px;
            color: $theme-color-dark-gray;
            padding-bottom: 10px;
        }

        &-select {
            width: 215px;
            font-size: 18px;
            color: $theme-color-dark-gray;
            height: 30px;
            margin-bottom: 12px;
            border: 1px solid $theme-color-dark-gray;
        }

        &-out-box {
            display: flex;
            align-items: flex-end;
            margin-bottom: 12px;
        }

        &-box {
            width: 100%;
            margin-right: 20px;
        }

        &-input {
            height: 26px;
            width: 100%;
            font-size: 18px;
            border-bottom: 1px solid $theme-color-dark-gray;
            color: $theme-color-dark-gray;
        }

        &-btn-box {
            text-align: right;
        }

        &-btn {
            width: 90px;
            height: 42px;
            font-size: 24px;
            color: $theme-color-white-blue;
            background-color: $theme-color-dark-gray;
             border-radius: 6px;
        }
    }

}

@media( max-width: 1023px ) {

    .service-request {
        flex-direction: column;

        .service-main {
            width: 100%;
            padding: 30px;

            .service-title {
                text-align: center;
            }

            .service-subtitle {
                text-align: center;
            }
        }

        .request {
            width: 100%;
            padding: 25px;

            &-text {

            }

            &-select {
                width: 100%;
            }

            &-out-box {
                flex-direction: column;
                padding: 0px;
            }

            &-box {
                margin-right: 0px;
            }

            &-input {

            }

            &-btn-box {
                padding-top: 20px;
            }

            &-btn {
                width: 60px;
                height: 40px;
                font-size: 12px;
            }
        }

    }

}

</style>



