<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.student-feeback {
  padding: 90px 75px;
  background-color: $theme-color-white-blue;

  .feeback {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 30px 0px;

    &-out-box {
      width: 410px;
      padding: 32px;
      margin: 15px;
      background-color: $theme-color-white;
    }

    &-box {
      display: flex;
      align-items: center;
    }

    &-img {
      width: auto;
      height: 48px;
      margin-right: 24px;
    }

    &intro {

    }

    &-title {
      text-align: left;
      color: $theme-color-dark-gray;
      font-size: 18px;
    }

    &-text {
      padding-top: 30px;
      color: $theme-color-dark-gray;
      font-size: 18px;
    }
  }
}

@media( max-width: 1023px ) {

.student-feeback {
  padding: 25px 25px 10px;

  .feeback {

    &-out-box {
      max-width: 300px;
      width: 90%;
      padding: 22px 10px 10px;
    }

    &-box {
      justify-content: center;
    }

    &-img {

    }

    &intro {

    }

    &-title {
      font-size: 14px;
    }

    &-text {
      padding-top: 0px;
      font-size: 14px;
    }
  }
}
}

</style>



