<template>
<div class="school-feature">
    <div id="commonFeature" class="feature">
        <h1 class="title"></h1>
        <div class="feature-out-box">
            <div class="feature-box">
                <div class="feature-title">
                    <img class="feature-img" src="@/assets/feature-house.png" alt="feature-house">
                    <div></div>
                </div>
                <div class="feature-text">
                    APCS<br>
                    APCS<br><br>
                    <br>
                    PythonAI<br><br>
                    AI<br>
                    <br><br>
                     <br>
                    <br><br>
                    <br>
                    AISynergiesSIS AI College<br><br>
                </div>
            </div>
            <div class="feature-box">
                <div class="feature-title">
                    <img class="feature-img" src="@/assets/feature-person.png" alt="feature-person">
                    <div></div>
                </div>
                <div class="feature-text">
                    AI<br>
                    AIPython<br><br>
                    <br>
                    108CSTA<br><br>
                     <br>
                    AIPython<br><br>
                    AI<br>
                    AIAI
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'SchoolFeature'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.school-feature {

    .feature {

        &-title {
            font-size: 26px;
            font-weight: 500;
            text-align: center;
        }

        &-out-box {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 30px 40px 90px;
        }

        &-box {
            display: flex;
            align-items: center;
            width: 70%;
        }

        &-img {
            width: 78px;
            margin: 0px 50px;
        }

        &-text {
            margin: 0px 25px;
            font-size: 18px;
        }

    }

}

@media( max-width: 1023px ) {

    .school-feature {

        .feature {

            &-title {
                font-size: 24px;
                margin: 30px 50px;
            }

            &-out-box {
                padding: 10px 10px 40px;
            }

            &-box {
                flex-direction: column;
                width: auto;
            }

            &-img {

            }

            &-text {

            }

        }

    }

}

</style>



