<template>
<div id="studentSchool" class="sis-school">
    <h1 class="title"></h1>
    <div class="sis-box">
      <p class="subtitle">2</p>
      <p class="subtitle">3  /  /  </p>
      <p class="subtitle">5 / </p>
    </div>
    <div class="school">
      <div class="school-left">

        <div class="school-title">
          <img class="school-img" src="@/assets/school-lamp.png" alt="lamp">
          <div></div>
        </div>
        <div class="school-left-box">
          <div class="school-left-title">
            <p class="school-left-title-inner school-left-title-general">AI</p>
            <P class="school-left-title-inner school-left-title-improve">AI</P>
          </div>
          <div class="school-left-main-text">
            <div class="school-left-inner-box">
              <div>
                <p>12hr</p>
                <div class="school-difficult">
                  <p></p>
                  <img class="school-star" src="@/assets/star.png" alt="star">
                </div>
                <p></p>
                <p>1.AI</p>
                <P>2.Python</P>
              </div>
            </div>
            <div class="school-left-inner-box school-deep-background">
              <div>
                <p>24hr</p>
                <div class="school-difficult">
                  <p></p>
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                </div>
                <p></p>
                <p>1.Python </p>
                <P>2.</P>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="school-right">
        <div class="school-title">
          <img class="school-img" src="@/assets/school-group.png" alt="lamp">
          <div></div>
        </div>
        <div class="school-right-box">
          <div class="school-right-title">AI</div>
          <div class="school-right-main-text">
            <div class="school-right-inner-box">
              <div>
                <p>36hr</p>
                <div class="school-difficult">
                  <p></p>
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                </div>
                <p></p>
                <p>1.</p>
                <p>2.</p>
                <p>3.</p>
              </div>
            </div>
            <div class="school-right-inner-box school-deep-background">
              <div>
                <p>36hr</p>
                <div class="school-difficult">
                  <p></p>
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                  <img class="school-star" src="@/assets/star.png" alt="star">
                </div>
                <p></p>
                <p>1.</p>
                <p>2.</p>
                <p>3.</p>
              </div>
            </div>
            <div class="school-right-inner-box-last">
              <p>36hr</p>
              <div class="school-difficult">
                <p></p>
                <img class="school-star" src="@/assets/star.png" alt="star">
                <img class="school-star" src="@/assets/star.png" alt="star">
                <img class="school-star" src="@/assets/star.png" alt="star">
                <img class="school-star" src="@/assets/star.png" alt="star">
                <img class="school-star" src="@/assets/star.png" alt="star">
              </div>
              <div class="school-right-last-content-box">
                <div class="school-right-last-content">
                  <p></p>
                  <p>1.</p>
                  <p>2.</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'SisSchool'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.sis-school {
  padding: 0px 90px 50px;

  .sis-box {
    width: 420px;
    margin: auto;
  }

  .sis-box p {
    width: 420px;
    font-size: 20px;
    text-align: left;
  }

  .school {
    display: flex;
    padding: 90px 0px;

    &-left {
      width: 40%;
    }

    &-title {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 30px;
      text-align: center;
      color: $theme-color-dark-black;
      margin-bottom: 12px;
    }

    &-img {
      height: 40px;
      margin-right: 5px;
    }

    &-left-title {
      display: flex;
      color: $theme-color-white;
      text-align: center;
      font-size: 18px;
    }

    &-left-title-inner {
      width: 50%;
      padding: 10px 0px;
      margin: 0px;
    }

    &-left-title-general {
      background-color: $theme-color-general-primary;
    }

    &-left-title-improve {

      background-color: $theme-color-light-dark-primary;
    }

    &-left-main-text {
      display: flex;
      font-size: 18px;
    }

    &-left-inner-box {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 50%;
      border: 1px solid rgba(244, 244, 248, 0.8);
      box-shadow: 1px 1px 9px 0 #f1f7f9;
      padding: 17px 8px;
      height: 262px;
    }

    &-difficult {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
    }

    &-star {
      margin-right: 3px;
    }

    &-deep-background {
       background-color: rgba(244, 244, 248, 0.3);
    }

    &-right {
      width: 59%;
    }

    &-right-box {

    }

    &-right-title {
      color: $theme-color-white;
      text-align: center;
      padding: 10px 0px;
      margin: 0px;
      background-color: $theme-color-dark-primary;
      font-size: 18px;
    }

    &-right-main-text {
      display: flex;
      font-size: 18px;
    }

    &-right-inner-box {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 33.3%;
      border: 1px solid rgba(244, 244, 248, 0.8);
      box-shadow: 1px 1px 9px 0 #f1f7f9;
      padding: 17px 8px;
      height: 262px;
    }

    &-right-last-content-box {
      height: 258px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &-right-inner-box-last {
      width: 33.3%;
      border: 1px solid rgba(244, 244, 248, 0.8);
      box-shadow: 1px 1px 9px 0 #f1f7f9;
      padding: 17px 8px;
      height: 262px;
    }
  }

}

@media( max-width: 1233px ) {
  .sis-school {

    .school {

      &-right-last-content-box {
          height: 233px;
      }
    }
  }
}

@media( max-width: 1080px ) {
  .sis-school {

    .sis-box {
      width: 280px;
    }

    .sis-box p {
      width: 280px;
    }

    .school {

      &-left-inner-box {
        height: 300px;
      }

      &-right-inner-box {

        height: 300px;
      }

      &-right-last-content-box {
        height: 300px;
      }

      &-right-inner-box-last {

        height: 300px;
      }

    }
  }
}

@media( max-width: 1023px ) {
  .sis-school {
    padding: 20px 0px 0px;

    .school {
      flex-direction: column;
      padding: 10px 0px 25px;

      &-left {
        width: 100%;
        margin-bottom: 24px;
      }

      &-title {
        font-size: 18px;
      }

      &-img {
        width: 30px;
        height: auto;
      }

      &-left-title {
        font-size: 16px;
      }

      &-left-title-inner {

      }

      &-left-title-general {

      }

      &-left-title-improve {

      }

      &-left-main-text {
        font-size: 14px;
      }

      &-left-inner-box {
        padding: 15px;
        height: 185px;
      }

      &-deep-background {

      }

      &-right {
        width: 100%;
      }

      &-right-box {

      }

      &-right-title {
        font-size: 16px;
      }

      &-right-main-text {
        flex-wrap: wrap;
        font-size: 14px;
      }

      &-right-inner-box {
        padding: 15px;
        height: 235px;
        width: 50%;
      }

      &-right-inner-box-last {
        padding: 15px;
        width: 100%;
        height: auto;
      }

      &-right-last-content-box {
        display: flex;
        flex-direction: row;
        height: auto;
      }

      &-right-last-content {
        width: 100%;
      }
    }
  }
}

</style>



