<template>
  <div class="student">
    <app-header :type="'student'"></app-header>
    <section-topic :type="'student'"></section-topic>
    <class-nav></class-nav>
    <golden-teacher></golden-teacher>
    <teacher-group></teacher-group>
    <train-result></train-result>
    <video-intro></video-intro>
    <student-feeback></student-feeback>
    <relate-news></relate-news>
    <school-banner></school-banner>
    <sis-school></sis-school>
    <apply-subject></apply-subject>
    <school-feature></school-feature>
    <core-concept :type="'student'"></core-concept>
    <world-consultant></world-consultant>
    <select-reward></select-reward>
    <service-request :type="'student'"></service-request>
  </div>
</template>

<script>
import AppHeader from '@/components/layout/AppHeader'
import SchoolBanner from '@/components/common/SchoolBanner'
import SectionTopic from '@/components/common/SectionTopic'
import VideoIntro from '@/components/common/VideoIntro'
import CoreConcept from '@/components/common/CoreConcept'
import TrainResult from '@/components/common/TrainResult'
import SchoolFeature from '@/components/common/SchoolFeature'
import WorldConsultant from '@/components/common/WorldConsultant'
import ServiceRequest from '@/components/common/ServiceRequest'
import SelectReward from '@/components/common/SelectReward'
import ClassNav from '@/pages/Student/components/ClassNav'
import GoldenTeacher from '@/pages/Student/components/GoldenTeacher'
import TeacherGroup from '@/pages/Student/components/TeacherGroup'
import SisSchool from '@/pages/Student/components/SisSchool'
import ApplySubject from '@/pages/Student/components/ApplySubject'
import StudentFeeback from '@/pages/Student/components/StudentFeeback'
import RelateNews from '@/pages/Student/components/RelateNews'

export default {
  name: 'Student',
  components: {
    AppHeader,
    SectionTopic,
    VideoIntro,
    ClassNav,
    GoldenTeacher,
    TeacherGroup,
    TrainResult,
    StudentFeeback,
    RelateNews,
    SchoolBanner,
    SisSchool,
    ApplySubject,
    SchoolFeature,
    CoreConcept,
    WorldConsultant,
    SelectReward,
    ServiceRequest
  },
  mounted () {
    window.scrollTo(0, 0)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

</style>



