<template>
<div @click="closeAllFloat" class="apply-subject">
    <h1 class="title">12</h1>
    <p class="subtitle"></p>
    <div class="subject">
        <single-subject
        v-for="(content, index) in contents"
        :key="index"
        :content="content"
        ref="singleChild"
        @closeFloat="closeAllFloat"
        >
        </single-subject>
    </div>

</div>
</template>

<script>
import SingleSubject from '@/pages/Student/components/SingleSubject'

export default {
  name: 'ApplySubject',
  components: {
    SingleSubject
  },
  data () {
    return {
      contents: [
        {img: 'subject-ai', title: '-AI', star: 1, text: ''},
        {img: 'subject-basic', title: '-python', star: 1, text: ''},
        {img: 'subject-python', title: 'Python', star: 2, text: ''},
        {img: 'subject-bike', title: '  ', star: 2, text: ''},
        {img: 'subject-house', title: '', star: 3, text: ''},
        {img: 'subject-messenger', title: '', star: 3, text: 'pythonDNN'},
        {img: 'subject-animal', title: '', star: 3, text: ''},
        {img: 'subject-face', title: '', star: 4, text: 'CNN'},
        {img: 'subject-ship', title: '', star: 4, text: ''},
        {img: 'subject-music', title: '', star: 4, text: 'AI'},
        {img: 'subject-emotion', title: '- ', star: 5, text: 'IMDb50,000  "'},
        {img: 'subject-chip', title: '', star: 5, text: ''}
      ]
    }
  },
  methods: {
    closeAllFloat () {
      for (let i = 0; i < 12; i++) {
        this.$refs.singleChild[i].closeFloating()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.apply-subject {
    padding: 0px 60px 60px;

    .subject {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        margin-top: 60px;
    }

}

@media( max-width: 1023px ) {

    .apply-subject {
        padding: 22px;

        .subject {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 30px;
        }
    }

}

</style>



