<template>
<div class="class-nav">
    <div class="nav-left-box">108<br>AI</div>
    <div class="nav-right-box">
        <div @click="scrollEvent('goldenTeacher')" class="nav-text"><br>Teacher Team</div>
        <div @click="scrollEvent('schoolBanner')" class="nav-text"><br>Competitive Course</div>
        <div @click="scrollEvent('coreConcept')" class="nav-text"><br>Training certificate</div>
    </div>
</div>
</template>

<script>

export default {
  name: 'ClassNav',
  methods: {
    scrollEvent (id) {
      document.getElementById(id).scrollIntoView({ behavior: 'smooth' })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.class-nav {
    width: 90%;
    display: flex;
    justify-content: center;
    padding: 60px 0px 60px;
    font-size: 22px;
    margin: auto;

    .nav-left-box {
        margin-right: 60px;
    }

    .nav-right-box {
        display: flex;
    }

    .nav-text {
        cursor: pointer;
        border-right: 1px solid black;
        padding: 0px 30px;
        text-align: center;

        &:last-child {
            border-right: 0px solid black;
        }

        &:hover {
          opacity: 0.8;
        }
    }

}

@media( max-width: 1023px ) {

    .class-nav {
        padding: 50px 0px 30px;
        flex-direction: column;
        text-align: center;

        .nav-left-box {
            margin-right: 0px;
        }

        .nav-right-box {
            flex-direction: column;
        }

        .nav-text {
            margin-top: 30px;
            border-right: 0px solid black;
        }

    }

}

</style>



