<template>
<div id="coreConcept" class="core-concept">
  <div class="banner"> x  x <span class="banner-hide">:  AI </span></div>
  <div class="core">
      <div class="core-industry core-box">
          <h2></h2>
          <p class="core-text">Synergies  AI MIT</p>
      </div>
      <div class="core-training core-box" :class="{ 'core-specify-border' : type==='enterprise' }">
          <h2></h2>
          <p class="core-text">MIT Synergies  AI </p>
      </div>
      <div class="core-ecosystem core-box">
          <h2></h2>
          <p class="core-text">Synergies</p>
      </div>
      <div class="core-transform core-box" :class="{ 'core-specify-border' : type==='student' }">
          <img class="core-star" src="@/assets/core-star.png" alt="core-star">
          <p></p>
        </div>
      <img class="core-logo" src="@/assets/core-concept.png" alt="core-concept">
  </div>
</div>
</template>

<script>
export default {
  name: 'CoreConcept',
  props: {
    type: { type: String, default: 'enterprise' }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.core-concept {

    .banner {
        width: 100%;
        height: 100px;
        background-image: url('../../assets/background-green.png');
        color: $theme-color-white;
        text-align: center;
        line-height: 100px;
        font-size: 36px;
    }

    .core {
        max-width: 1200px;
        width: 100%;
        height: 600px;
        position: relative;
        margin: auto;

        &-box {
            width: 370px;
            height: 230px;
            padding: 11px;
        }

        &-industry {
            position: absolute;
            left: 48px;
            top: 90px;
        }

        &-training {
            position: absolute;
            left: 48px;
            bottom: 40px;

        }

        &-ecosystem {
            position: absolute;
            right: 60px;
            bottom: 40px;
        }

        &-transform {
            display: flex;
            align-items: flex-start;
            position: absolute;
            right: 60px;
            top: 130px;
            font-size: 24px;
            color: $theme-color-primary;
            height: auto;
        }

        &-specify-border {
            border-radius: 12px;
            box-shadow: 1px 1px 3px 0 $theme-color-light-blue;
            border: 3px dashed $theme-color-light-primary;
        }

        &-star {
            width: 60px;
            height: auto;
            margin-right: 10px;
        }

        &-logo {
            position: absolute;
            left: 50%;
            top: 50%;
            width: 300px;
            transform: translate(-50%, -50%) ;
        }

        &-text {
            font-size: 18px;
        }

    }

}

@media( max-width: 1150px ) {

    .core-concept {

        .core {

            &-logo {
                width: 250px;
            }

            &-box {
                width: 310px;
            }

            &-star {

            }
        }
    }
}

@media( max-width: 1023px ) {

    .core-concept {

        .banner {
            height: 30px;
            line-height: 30px;
            font-size: 14px;

            &-hide {
                display: none;
            }
        }

        .core {
            height: 800px;

            &-box {
                font-size: 14px;
                height: auto;
                left: 50%;
                transform: translateX(-50%) ;
            }

            &-industry {
                top: 650px;
            }

            &-training {
                top: 365px;
                height: 146px;
            }

            &-ecosystem {
                top: 510px;
                height: 160px;
            }

            &-transform {
                top: 24px;
            }

            &-logo {
                top: 242px;
            }

            &-star {
                width: 20px;
                margin-top: 8px;
            }

            &-text {
                font-size: 14px;
            }

        }

    }

}

</style>



