<template>
<div id="commonTop" class="section-topic">
  <div class="topic" :class="{ 'topic-student-img': type === 'student', 'topic-enterprise-img': type === 'enterprise' }">
    <template v-if="type === 'enterprise'">
        <div class="topic-title">AI</div>
        <p class="topic-subtitle"> |  </p>
        <p class="topic-subtitle"></p>
    </template>
    <template v-else-if=" type === 'student'">
        <img class="topic-text-img" src="@/assets/sis-ai-college.png" alt="sis-ai-college">
        <div class="topic-title"></div>
        <p class="topic-subtitle"></p>
        <p class="topic-child-subtitle">AI</p>
        <div class="topic-copyright">
            <p>AI training with industrial expert rather than School</p>
        </div>
    </template>
  </div>
  <!-- <div id="strategy" class="strategy">
      <img class="strategy-logo" src="@/assets/sis-logo.png" alt="logo">
    <div class="strategy-box">
        <p class="strategy-text">AI</p>
        <p class="strategy-text"></p>
    </div>
  </div> -->
</div>
</template>

<script>
export default {
  name: 'SectionTopic',
  props: {
    type: { type: String, default: 'enterprise' }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.section-topic {

    .topic {
        position: relative;
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;

        &-student-img {
            background-image: url('../../assets/student-main.png');
        }

        &-enterprise-img {
            background-image: url('../../assets/enterprise-main.png');
        }

        &-text-img {
            width: 280px;
            margin-bottom: 16px;
        }

        &-title {
            color: $theme-color-light-white;
            font-size: 60px;
            position: relative;
            margin-bottom: 20px;
            letter-spacing: 28px;
        }

        &-img {
            width: 900px;
        }

        &-subtitle {
            font-size: 30px;
            color: $theme-color-light-white;
            margin-bottom: 0px;
            letter-spacing: 16px;
            padding-left: 14px;
        }

        &-child-subtitle {
            font-size: 30px;
            color: $theme-color-light-white;
            margin-bottom: 0px;
            letter-spacing: 3px;
        }

        &-copyright {
            width: 312px;
            position: absolute;
            left: 50%;
            bottom: 40px;
            transform: translateX(-50%);
            color: $theme-color-light-white;
            text-align: center;
        }
    }

    .strategy {
        width: 100%;
        height: 248px;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        color: $theme-color-primary;

        &-logo {
            height: 150px;
            width: auto;
        }

        &-box {

        }

        &-text {
            font-size: 30px;
        }

    }

}

@media( max-width: 1023px ) {

    .section-topic {

        .topic {

            &-title {
                font-size: 22px;
                letter-spacing: 6px;

                &::before {
                    left: -10px;
                }

                &::after {
                    right: -10px;
                }
            }

            &-subtitle {
                font-size: 16px;
                letter-spacing: 10px;
            }

            &-child-subtitle {
                font-size: 16px;
            }

            &-copyright {
                font-size: 14px;
                display: flex;
                flex-direction: column;
            }

            &-copyright p {
                margin: 0px;
            }

            &-img {
                width: 280px;
            }

        }

        .strategy {
            height: 230px;
            justify-content: center;
            flex-direction: column-reverse;

            &-logo {
                height: 120px;
                padding: 20px;
            }

            &-box {

            }

            &-text {
                font-size: 18px;
            }

        }

    }

}

</style>



