<template>
<div id="schoolBanner" class="school-banner">
    <div class="banner">
        <p class="banner-title">108</p>
        <p class="banner-subtitle">145CSTA</p>
        <p class="banner-subtitle">NGSS&lt;K12></p>
    </div>
</div>
</template>

<script>
export default {
  name: 'SchoolBanner'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.school-banner {

    .banner {
        height: 268px;
        background-color: $theme-color-white-blue;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-bottom: 90px;

        &-title {
            font-size: 42px;
            font-weight: 500;
            color: $theme-color-dark-blue;
        }

        &-subtitle {
            font-size: 30px;
            font-weight: 500;
            color: $theme-color-dark-gray;
        }
    }
}

@media( max-width: 1023px ) {

    .school-banner {

        .banner {
            height: 140px;
            margin-bottom: 30px;

            &-title {
                font-size: 16px;
            }

            &-subtitle {
                font-size: 14px;
            }
        }

    }

}

</style>



