<template>
<div class="select-reward">
    <div class="select">
        <h1 class="title"></h1>
        <p class="subtitle"></p>
        <div class="reward">
            <div class="reward-box">
                <img class="reward-img" src="@/assets/award-fish.png" alt="award-fish">
                <div class="reward-up-text">US Top 100</div>
                <div class="reward-down-text">AI</div>
            </div>
            <div class="reward-box">
                <img class="reward-img" src="@/assets/award-mvp.png" alt="award-fish">
                <div class="reward-up-text">MVP 100</div>
                <div class="reward-down-text"></div>
            </div>
            <div class="reward-box">
                <img class="reward-img" src="@/assets/award-cio.png" alt="award-fish">
                <div class="reward-up-text">CIO Review</div>
                <div class="reward-down-text">20</div>
            </div>
            <div class="reward-box">
                <img class="reward-img" src="@/assets/award-robot.png" alt="award-fish">
                <div class="reward-up-text">Robotics Award2019 Final 10</div>
                <div class="reward-down-text"></div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'SelectReward'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.select-reward {

    .reward {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 90px 100px;

        &-box {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 15px;
        }

        &-img {
            width: auto;
            height: 160px;
        }

        &-up-text {
            font-size: 24px;
            color: $theme-color-dark-black;
            text-align: center;
            padding: 20px 0px 16px;
            height: 80px;
            line-height: initial;
        }

        &-down-text {
            font-size: 16px;
            color: $theme-color-light-gray;
            text-align: center;
            height: 60px;
            line-height: initial;
        }

    }

}

@media( max-width: 1023px ) {

    .select-reward {

        .reward {
            flex-direction: column;
            padding: 18px;

            &-box {

            }

            &-img {

            }

            &-up-text {
                height: auto;
                font-size: 18px;
            }

            &-down-text {
                font-size: 16px;
                line-height: 26px;
                height: auto;
            }
        }

    }

}

</style>



