<template>
  <div class="video-intro">
    <h1 class="title"></h1>
    <div class="video">
      <div class="video-inner">
        <iframe width="100%" height="100%" src="https://www.youtube.com/embed/YDQoBE4UzUs?rel=0" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'VideoIntro'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.video-intro {
  margin-top: 15px;

  .video {
    position: relative;
    width: 80%;
    height: 0;
    padding-top: 56.25%;
    margin: 40px auto 60px;

    &-inner {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
}

@media( max-width: 1023px ) {

  .video-intro {
    .video {
      margin: 20px auto 30px;
    }
  }
}

</style>



