<template>
<div class="relate-news">
    <h1 class="title"></h1>
    <div class="news">
      <swiper :options="swiperOption">
        <swiper-slide
          v-for="(rss, index) in rssData"
          :key="index"
        >
          <a class="news-inner-box" target="_blank" :href="rss.link">
            <div class="news-img-box">
              <img class="news-img" :src="rss.thumbnail" alt="img">
            </div>
            <div class="news-title">{{rss.title}}</div>
            <div class="news-date">{{rss.pubDate}}</div>
          </a>
        </swiper-slide>
      </swiper>

    </div>
</div>
</template>

<script>
import axios from 'axios'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'

export default {
  name: 'RelateNews',
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      rssData: {},
      swiperOption: {
        slidesPerView: 'auto',
        mousewheel: false,
        observer: true,
        observeParents: true
      }
    }
  },
  mounted () {
    axios.get(`//api.rss2json.com/v1/api.json?rss_url=https%3A%2F%2Fmedium.com%2Ffeed%2F%40sisaicollege&timestamp=${new Date().getTime()}`
    )
      .then(response => {
        this.rssData = response.data.items
        console.log(response)
      })
      .catch(error => {
        console.log(error)
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.relate-news {
    padding-top: 30px;

  .news {
    margin: 60px 0px;

    &-inner-box {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 470px;
      padding: 30px 30px 12px;
      color: $theme-color-dark-gray;
      box-shadow: 3px 3px 9px 0 $theme-color-light-blue;
      background-color: $theme-color-white;
      margin: 0px 30px;
    }

    .swiper-slide {
      width: 360px;
    }

    &-img-box {
      width: 100%;
      height: 150px;
      text-align: center;
      overflow: hidden;
    }

    &-img {
      height: 100%;
      align-self: center;
    }

    &-title {
      font-size: 30px;
    }

    &-date {
      font-size: 18px;
    }
  }
}

@media( max-width: 1023px ) {

  .relate-news {

    .news {

      &-inner-box {
        height: 300px;
        padding: 18px 18px 12px;
      }

      .swiper-slide {
        width: 256px;
      }

      &-img-box {
       height: 120px;
      }

      &-title {
        font-size: 18px;
      }

      &-date {
        font-size: 14px;
      }
    }

  }

}

</style>



