<template>
<div class="world-consultant">
    <h1 class="title">MIT </h1>
    <div class="consultant">

        <div class="consultant-row">
            <div class="consultant-out-box">
                <img class="consultant-img" src="@/assets/person-huang.png" alt="person-huang">
                <div class="consultant-box">
                    <div class="consultant-name"> Dan Wong</div>
                    <div class="consultant-experience">MBA</div>
                    <div class="consultant-experience">Synergies</div>
                    <div class="consultant-experience">MSC</div>
                    <div class="consultant-experience">Madhouse</div>
                </div>
            </div>
            <div class="consultant-out-box">
                <img class="consultant-img" src="@/assets/person-pei.png" alt="person-huang">
                <div class="consultant-box">
                    <div class="consultant-name"> Richard</div>
                    <div class="consultant-experience">()</div>
                    <div class="consultant-experience"></div>
                    <div class="consultant-experience">AIoT</div>
                    <div class="consultant-experience">AI</div>
                </div>
            </div>
            <div class="consultant-out-box">
                <img class="consultant-img" src="@/assets/person-ju.png" alt="person-huang">
                <div class="consultant-box">
                    <div class="consultant-name"> David</div>
                    <div class="consultant-experience">MITMBA</div>
                    <div class="consultant-experience">Synergies</div>
                    <div class="consultant-experience">MIT</div>
                    <div class="consultant-experience"> Maxis Capital</div>
                </div>
            </div>
        </div>

        <div class="consultant-row">
            <div class="consultant-out-box">
                <img class="consultant-img" src="@/assets/person-lu.png" alt="person-huang">
                <div class="consultant-box">
                    <div class="consultant-name"></div>
                    <div class="consultant-experience"></div>
                    <div class="consultant-experience"></div>
                    <div class="consultant-experience"></div>
                    <div class="consultant-experience"></div>
                </div>
            </div>
            <div class="consultant-out-box">
                <img class="consultant-img" src="@/assets/person-chang.png" alt="person-huang">
                <div class="consultant-box">
                    <div class="consultant-name"> Michael</div>
                    <div class="consultant-experience">MIT</div>
                    <div class="consultant-experience">Synergies</div>
                    <div class="consultant-experience">Kyper Data</div>
                    <div class="consultant-experience"></div>
                </div>
            </div>
            <div class="consultant-out-box">
                <img class="consultant-img" src="@/assets/person-wei.png" alt="person-huang">
                <div class="consultant-box">
                    <div class="consultant-name"></div>
                    <div class="consultant-experience">AI</div>
                    <div class="consultant-experience"></div>
                    <div class="consultant-experience">Synergies</div>
                    <div class="consultant-experience"></div>
                </div>
            </div>
        </div>

    </div>
</div>
</template>

<script>
export default {
  name: 'WorldConsultant'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.world-consultant {

    .consultant {
        padding: 60px 120px 45px;

        &-row {
            display: flex;
            justify-content: space-evenly;
            padding-bottom: 60px;
        }

        &-out-box {
            display: flex;
            align-items: center;
        }

        &-img {
            width: 180px;
            height: auto;
        }

        &-box {
            width: 270px;
        }

        &-name {
            font-size: 18px;
            padding: 18px;
            color: $theme-color-white;
            background-color: $theme-color-dark-primary;
            border-top-right-radius: 6px;
        }

        &-experience {
            font-size: 16px;
            padding: 18px 0px 0px 18px;
            color: $theme-color-light-gray;
        }
    }

}

@media( max-width: 1370px ) {

    .world-consultant {

        .consultant {

            &-img {
                width: 120px;
            }

            &-box {
                width: 210px;
            }

        }
    }

}

@media( max-width: 1023px ) {

    .world-consultant {

        .consultant {
            padding: 24px;

            &-row {
                padding-bottom: 0px;
                flex-direction: column;
            }

            &-out-box {
                justify-content: center;
                padding-bottom: 18px;
            }

            &-img {

            }

            &-box {

            }

            &-name {
                font-size: 16px;
                padding: 4px;
            }

            &-experience {
                font-size: 14px;
                padding: 8px;
            }
        }

    }
}

</style>



