<template>
  <div class="header" :class="{ 'header-opacity': isOpacity }">
    <!-- <router-link to="/"> -->
      <img class="header-logo" src="@/assets/logo.png" alt="">
    <!-- </router-link> -->
    <div v-if="isShowMenu" class="header-right">
      <div @click="scrollEvent('commonTop')" class="header-text">TOP</div>
      <!-- <router-link v-if="type === 'enterprise'" to="/student">
        <div class="header-text header-specify"></div>
      </router-link>
      <router-link v-if="type === 'student'" to="/enterprise">
        <div class="header-text header-specify"></div>
      </router-link> -->
      <!-- <div @click="scrollEvent('commonFeature')" class="header-text"></div> -->
      <div v-if="type === 'enterprise'" @click="scrollEvent('enterpriseTeaching')" class="header-text"></div>
      <div v-if="type === 'enterprise'" @click="scrollEvent('enterpriseCase')" class="header-text"></div>
      <div v-if="type === 'student'" @click="scrollEvent('goldenTeacher')" class="header-text"></div>
      <div v-if="type === 'student'" @click="scrollEvent('studentSchool')" class="header-text"></div>
      <div v-if="type === 'student'" @click="scrollEvent('commonFeature')" class="header-text"></div>
      <div v-if="type === 'student'" @click="scrollEvent('commonService')" class="header-text"></div>
      <!-- <div @click="scrollEvent('commonService')" class="header-text"></div> -->
    </div>
    <img @click="isShowMenu = !isShowMenu" class="header-menu" src="@/assets/menu.png" alt="menu">
  </div>
</template>

<script>
export default {
  name: 'AppHeader',
  props: {
    type: { type: String, default: 'enterprise' }
  },
  data () {
    return {
      isShowMenu: true,
      isOpacity: false,
      screenWidth: document.documentElement.clientWidth
    }
  },
  mounted () {
    this.judgeScreenWidth()
    window.addEventListener('scroll', this.handleScroll)
    window.onresize = () => {
      this.judgeScreenWidth()
    }
  },
  methods: {
    scrollEvent (id) {
      document.getElementById(id).scrollIntoView({ behavior: 'smooth' })
    },
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      let offsetTop = document.querySelector('.topic-title').offsetTop
      if (scrollTop > offsetTop - 30) {
        this.isOpacity = true
      } else {
        this.isOpacity = false
      }
    },
    judgeScreenWidth () {
      this.screenWidth = document.documentElement.clientWidth
      if (this.screenWidth < 1024) {
        this.isShowMenu = false
      } else {
        this.isShowMenu = true
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.header {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 2;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to top, #005055, #000000);
  height: 90px;

  &-opacity {
    opacity: 0.8;
  }

  &-logo {
    height: 40px;
    margin: 12px 0px 0px 48px;
    // cursor: pointer;

    // &:hover {
    //   opacity: 0.8;
    // }
  }

  &-right {
    display: flex;
    margin: 12px 48px 0px 0px;
  }

  &-text {
    color: $theme-color-white;
    font-size: 22px;
    padding-right: 20px;
    cursor: pointer;

    &:hover {
      opacity: 0.8;
    }
  }

  &-specify {
    padding-right: 50px;
  }

  &-menu {
    display: none;
    margin: 12px 48px 0px 0px;
  }
}

@media( max-width: 1023px ) {

  .header {
    height: 30px;

    &-logo {
      height: 12px;
      margin: 8px 0px 0px 24px;
    }

    &-right {
      // display: none;
      display: block;
      margin: 0px;
      position: fixed;
      top: 30px;
      width: 100%;
      height: 100%;
      background-color: $theme-color-black;
      opacity: 0.8;
    }

    &-text {
      font-size: 14px;
      margin: 10px;
    }

    &-specify {
      margin-bottom: 20px;
    }

    &-menu {
      display: block;
      width: 14px;
      margin: 4px 24px 0px 0px;
      cursor: pointer;
    }

  }

}

</style>



