<template>
<div class="train-result">
    <div class="train">
        <h1 class="title">2</h1>
        <div class="train-out-box">
            <div class="train-box train-border">
                <p class="train-up-text">800+</p>
                <p class="train-down-text"></p>
            </div>
            <div class="train-box train-border">
                <p class="train-up-text">300+</p>
                <p class="train-down-text">K12</p>
            </div>
            <div class="train-box train-border">
                <p class="train-up-text">500+</p>
                <p class="train-down-text"></p>
            </div>
            <div class="train-box">
                <p class="train-up-text">200+</p>
                <p class="train-down-text">500</p>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'TrainResult'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  rel="stylesheet/scss" lang="scss" scoped>

.train-result {
    padding: 30px 0px;

    .train {
        padding: 30px;
        background-color: rgba(244, 244, 248, 0.3);

        &-out-box {
            display: flex;
            align-items: center;
            justify-content: center;
            padding-top: 60px;
        }

        &-box {
            padding: 0px 60px;
            font-weight: 500;
        }

        &-border {
            border-right: 1px solid $theme-color-primary;
        }

        &-up-text {
            font-size: 42px;
            color: $theme-color-primary;
        }

        &-down-text {
            font-size: 30px;
            color: $theme-color-primary;
        }
    }

}

@media( max-width: 1023px ) {

    .train-result {
        padding: 0px;

        .train {
        padding: 30px 0px 5px;

            &-out-box {
                padding-top: 12px;
                flex-wrap: wrap;
            }

            &-box {
                padding: 0px;
                width: 50%;
                text-align: center;
                margin-bottom: 30px;
                width: 150px;
            }

            &-border {
                border-right: 0px;
            }

            &-up-text {
                font-size: 30px;
            }

            &-down-text {
                font-size: 18px;
            }
        }

    }

}

</style>



